header {
  width: 100%;
  box-shadow: 0 2px 4px  var(--shadows);
  background-color: var(--elements);
}

.header-contents {
  max-width: 1290px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 0;

  /* center header elements */
  margin-left: auto;
  margin-right: auto;

}



@media (min-width: 0px) {
  h1 {
    font-size: 0.85rem;  
    color: var(--text-primary);
  }

  .header-contents {
    padding: 1.5rem 1rem;
  }
}

@media (min-width: 600px) {
  h1 {
    font-size: 1.5rem;
    font-weight: 800;
  }
}